.forest #idealist, #clusterlist{
	border: 1px solid #ddd;
	border-radius: 5px;
	background-color: #f5f5f5;
	margin-left: 0px;
}

.forest #confirmModal, #nameModal {
  top: 15%;
  left: 50%;
  height: 1px;
  width: 600px;
}

.forest #node-drag-target, .stack, .donestack{
	/* margin-left: 0px; */
}

.forest .donestack > .idea-item, .cluster-item{
	/*display: none;*/
}

.forest .idea-item, .donestack, .stack{
	border: solid 1px #ddd;
	border-radius: 5px;
	list-style: none;
	list-style-type: none;
}

.forest ul {
	list-style: none;
	list-style-type: none;
}

.forest li.ui-sortable-helper{
	cursor: move;
}

.forest li.sort-disabled{
	border: none;
	background-color: #f5f5f5;
	height:0px;
	padding:1px;
}

.forest .newstack{
	border: dashed 1px #ddd;
	border-radius: 5px;
	padding: 5px;
}

.forest .clusternodename{
	width: 242px;
	margin: 5px;
}

label, .fa{
	/*padding-left: 5px;*/
	padding-top: 5px;
}

.donestack{
	background-color: #f5f5f5;
}

#tree{
	height: 675px;
	overflow: auto;
}

#nodestatus, #generalize, #buildcluster{
	display:none;
}

#nomatch{
	margin-top: 50px;
}

#generalize > button{
	margin-top: 10px;
}
